
.grid {
  padding: 1.3% 8px;
}
.grid .grid_box {
  overflow: hidden;
}
.grid .grid_box li {
  width: 25%;
  box-sizing: border-box;
  text-align: center;
  float: left;
  cursor: pointer;
}
.grid .grid_box li:hover img {
  box-shadow: 0 20px 40px -10px rgba(0, 0, 0, 0.2);
}
.grid .grid_box li:hover h4 {
  color: #212121;
}
.grid .grid_box li img {
  width: 95%;
  height: 228px;
  border-radius: 4px;
  transition: all 0.2s ease-out;
}
.grid .grid_box li h4 {
  width: 85%;
  font-size: 18px;
  color: #5c6670;
  text-align: left;
  margin: 12px auto 0;
  transition: all 0.2s ease-out;
}
.grid .grid_box li p {
  width: 85%;
  font-size: 14px;
  color: #a4a9ad;
  text-align: left;
  margin: 3px auto 0;
}



