}
.detailComment .commititem {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  border-bottom: 1px solid #e5e5e5;
}
.detailComment .louceng {
  font-size: 14px;
