}
.commentpub {
  width: 1100px;
  margin: 0 auto;
  background: #fff;
  padding: 30px 20px;
}
.rightitems {
  position: fixed;
  left: 50%;
  top: 200px;
  margin-left: 590px;
}
.rightitems ul {
  border-radius: 4px;
  margin-top: 20px;
  overflow: hidden;
  color: #939499;
  line-height: 22px;
}
.rightitems ul li {
  background-color: #e9e9e9;
  width: 60px;
  height: 48px;
  cursor: pointer;
  line-height: 48px;
  border-bottom: 1px solid #dadada;
  text-align: center;
  transition: all 0.1s ease-out;
  -webkit-transition: all 0.1s ease-out;
  font-size: 14px;
}
.rightitems ul li:hover {
  color: #fff;
  background-color: #a4a9ad;
}
.rightitems ul li.return {
  border-bottom: none;
  background: #e9e9e9 url(../../assets/community/return.png) center center no-repeat;
  background-size: 28px 28px;
}
.rightitems ul li.return:hover {
  background: #a4a9ad url(../../assets/community/returnActive.png) center center no-repeat;
  background-size: 32px 32px;
}
.rightitems ul li.gotop {
  border-bottom: none;
  background: #e9e9e9 url(../../assets/community/returnTop.png) center center no-repeat;
  background-size: 36px 20px;
}
.rightitems ul li.gotop:hover {
  background: #a4a9ad url(../../assets/community/returnTopActive.png) center center no-repeat;
  background-size: 40px 24px;
}
.commentmodal {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
.commentmodal .commentbox {
  transform: translateY(40%);
  width: 680px;
  height: 400px;
  box-sizing: border-box;
  border-radius: 5px;
  background: rgba(0, 0, 0, 0.2);
  margin: 0 auto;
  padding: 10px;
}
.commentmodal .commentbox .commentcontainer {
  background: #fff;
  height: 100%;
  width: 100%;
}
.commentmodal .commentbox .commentcontainer .commentbox-head {
  padding: 10px 10px 8px;
  height: 20px;
  line-height: 20px;
  font-size: 14px;
  font-weight: 700;
  color: #939499;
}
.commentmodal .commentbox .commentcontainer .commentbox-head i {
  float: right;
  width: 20px;
  height: 20px;
  overflow: hidden;
  text-indent: -9999px;
  background: url(../../assets/community/cls.gif) no-repeat 0 0;
  cursor: pointer;
}
.commentmodal .commentbox .commentcontainer .commentbox-head i:hover {
  background-position: 0 -20px;
}
.btngroup {
  text-align: center;
  margin-top: 20px;
}
.btngroup button {
  width: 100px;
  height: 40px;
  border-radius: 4px;
  color: #fff;
  background-color: #e60e19;
  border: 1px solid #d94030;
  font-size: 14px;
  text-align: center;
  outline: none;
  cursor: pointer;
}
.btngroup button.nodisbalebtn {
  background-color: #bbb;
  border: none;
  cursor: default;
}



