
.publish-wrap {
  margin: 100px auto 0;
}
.publish-wrap:after {
  content: "";
  display: block;
  clear: both;
}
.ql-container .ql-editor {
  height: 300px;
  min-height: 300px;
  padding-bottom: 1em;
}
body {
  background: #F5F5F5;
}
.publish .publishbox {
  margin: 0 auto;
  width: 1140px;
  padding: 60px 90px;
  border-radius: 5px;
  background: #fff;
}
.publish .publishbox .pupublishbox-title {
  font-weight: normal;
  color: #df001f;
}
.publish .publishbox .topicbox {
  margin: 20px 0;
}
.publish .publishbox .topicbox .topic-title {
  outline: none;
  width: 638px;
  height: 34px;
  line-height: 34px;
  border-radius: 4px;
  border: 1px solid #e6e6e6;
  text-indent: 2em;
  font-size: 14px;
}
.publish .publishbox .btngroup {
  text-align: center;
  margin-top: 50px;
}
.publish .publishbox .btngroup button {
  width: 100px;
  height: 40px;
  border-radius: 4px;
  color: #fff;
  background-color: #e60e19;
  border: 1px solid #d94030;
  font-size: 14px;
  text-align: center;
  outline: none;
  cursor: pointer;
}



